import { Container, Grid, Typography, TableRow, Box, List, Divider } from '@mui/material';
import { useContext } from 'react';
import { MainContext } from '../Contexts/MainContext';

export default function PrintCaracs() {

    const {
        filteredData,
        displayCaracValue
    } = useContext(MainContext)

    if (filteredData && Array.isArray(filteredData)) {

        return (
            <Container sx={{ displayPrint: displayCaracValue, display: 'none' }} maxWidth="100vw"  >
                <Box>
                    <Grid container spacing={1} marginBottom={1}>
                        <Grid item xs={1.5} md={2} >
                            <Typography variant='caption'>Nom</Typography>
                        </Grid>
                        <Grid item xs={0.5} md={0.5}>
                            <Typography variant='caption'>Type</Typography>
                        </Grid>
                        <Grid item xs={1} md={0.5}>
                            <Typography variant='caption'>Vit</Typography>
                        </Grid>
                        <Grid item xs={0.5} md={0.5}>
                            <Typography variant='caption'>Bl</Typography>
                        </Grid>
                        <Grid item xs={0.5} md={0.5}>
                            <Typography variant='caption'>CC</Typography>
                        </Grid>
                        <Grid item xs={0.5} md={0.5}>
                            <Typography variant='caption'>FF</Typography>
                        </Grid>
                        <Grid item xs={4.5} md={4}>
                            <Typography variant='caption'>Armes / Portée / Puissance</Typography>
                        </Grid>
                        <Grid item xs={3} md={3.5}>
                            <Typography variant='caption' >Notes</Typography>
                        </Grid>
                    </Grid>
                    <Grid container spacing={1} >
                        {filteredData.map((row, index) => (

                            <Grid container item key={index} xs={12} alignItems={'center'} style={{
                                borderTop: 1,
                                backgroundColor: index % 2 === 0 ? '#e5eef5' : 'white', // Condition pour l'arrière-plan alternatif
                            }}>
                                <Grid item xs={1.5} md={2}  >
                                    <Typography variant='caption'>{row.Nom}</Typography>
                                </Grid>
                                <Grid item xs={0.5} md={0.5}>
                                    <Typography variant='caption'>{row.Type}</Typography>
                                </Grid>
                                <Grid item xs={1} md={0.5}>
                                    <Typography variant='caption'>{row.Vitesse}</Typography>
                                </Grid>
                                <Grid item xs={0.5} md={0.5}>
                                    <Typography variant='caption'>{row.Blindage}</Typography>
                                </Grid>
                                <Grid item xs={0.5} md={0.5}>
                                    <Typography variant='caption'>{row.CC}</Typography>
                                </Grid>
                                <Grid item xs={0.5} md={0.5}>
                                    <Typography variant='caption'>{row.FF}</Typography>
                                </Grid>
                                <Grid item xs={4.5} md={4}>
                                    <Grid Container alignContent={'center'}>
                                        {row.armes.map((arme, idx) => (
                                            <Grid item xs={12} >
                                                <Typography key={idx} variant='caption' sx={{ fontSize: "7pt" }}>{arme}</Typography>
                                            </Grid>
                                        ))}
                                    </Grid>
                                </Grid>
                                <Grid item xs={3} md={3.5}>
                                    <Typography variant='caption' sx={{ fontSize: "8pt" }}>{row.Notes}</Typography>
                                </Grid>
                            </Grid>

                        ))}
                    </Grid>
                </Box >
            </Container >

        )
    }
}