import React, { useContext, useState } from 'react';
import AddIcon from '@mui/icons-material/Add';
import OpenInFullIcon from '@mui/icons-material/OpenInFull';
import CloseFullscreenIcon from '@mui/icons-material/CloseFullscreen';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import CleaningServicesIcon from '@mui/icons-material/CleaningServices';
import CloseRoundedIcon from '@mui/icons-material/CloseRounded';
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
import {
    Accordion, AccordionDetails, AccordionSummary, Alert, Box, Button,
    Card, CardContent, Container, Dialog, DialogActions,
    DialogContent, DialogContentText, DialogTitle, Divider, FormControl,
    Grid, IconButton, List, ListItem, ListItemText, MenuItem,
    Select, Stack, Typography, SpeedDial
} from '@mui/material';
import { MainContext } from '../Contexts/MainContext';
import VisibilityIcon from '@mui/icons-material/Visibility';
import { styled } from '@mui/material/styles';
import InfoIcon from '@mui/icons-material/Info';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
import StarsIcon from '@mui/icons-material/Stars';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import FormatListBulletedIcon from '@mui/icons-material/FormatListBulleted';

export default function MainPage() {
    //Style pour le tooltip
    const NoMaxWidthTooltip = styled(({ className, ...props }) => (
        <Tooltip {...props} classes={{ popper: className }} />
    ))({
        [`& .${tooltipClasses.tooltip}`]: {
            maxWidth: 'none !important',
        },
    });

    const {
        alertRare,
        handleToggleAllAccordions,
        formattedInfos,
        updateInfos,
        handleSelectCodex,
        handleOptionRemove,
        handleRemoveCarte,
        handleRemoveAllCartes,
        handleShowFormationOptions,
        handleCarteSelect,
        handleOptionSelect,
        handleFormationAndCreateCarte,
        handleShowOptions,
        formations,
        selectedFormationOptions,
        selectedCarteIndex,
        showCodexSelection,
        CodexSelectionPopup,
        handleCloseCodexSelection,
        selectedCodex,
        cartes,
        calculateCarteValue,
        open,
        handleClose,
        handleClickOpen,
        accordionStates,
        handleToggleAccordion,
        totalRareValue,
        scrollToSection
    } = useContext(MainContext)
    const [drawerOpen, setDrawerOpen] = useState(false);
    const toggleDrawer = (open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        setDrawerOpen(open);
    };


    return (
        <Container maxWidth="100vw"  sx={{ displayPrint: 'none', height: '100vw', marginTop:15 }} >         

            {showCodexSelection && (
                <CodexSelectionPopup onClose={handleCloseCodexSelection} onSelectCodex={handleSelectCodex} />
            )}
            {selectedCodex && (
                
                <Stack spacing={2} top="500px" >
                    {/* Colonne formation */}
                    <Box item xs={12} md={4} >
                        <Card>
                            <CardContent >
                                <Typography variant="h4">Formations</Typography>
                                <List>
                                    {selectedCodex &&
                                        formations.map((group, groupIndex) => (
                                            <div key={groupIndex}>
                                                <ListItemText>
                                                    <Typography variant='h6'>{group.groupName} </Typography>
                                                    <Typography variant='caption'>{group.detail}</Typography>
                                                </ListItemText>
                                                <List >
                                                    {group.formations.map((formation, formationIndex) => (
                                                        <ListItem sx={{ margin: 0, padding: 0 }} key={formationIndex} >
                                                            <IconButton size="small" color="default" onClick={() => handleShowOptions(formation)}>
                                                                <VisibilityIcon></VisibilityIcon>
                                                            </IconButton>
                                                            <Typography variant='body1'>{formation.name}</Typography>
                                                            <IconButton size="small" color="success" onClick={() => {
                                                                handleFormationAndCreateCarte(formation);
                                                                scrollToSection();
                                                            }}>
                                                                <AddIcon></AddIcon>
                                                            </IconButton>
                                                        </ListItem>
                                                    ))}
                                                </List>
                                            </div>
                                        ))}
                                </List>
                            </CardContent>
                        </Card>
                    </Box>
                    {/* Colonne option */}
                    <Box item xs={12} md={4} id="options">
                        <Card sx={{ position: 'sticky', top: 100, zIndex: 1 }}>
                            <CardContent>
                                <Typography variant="h4">Options</Typography>
                                <Stack>
                                    {selectedFormationOptions &&
                                        selectedFormationOptions.map((option, optionIndex) => (
                                            <List  >
                                                <ListItem sx={{ margin: 0, padding: 0 }} key={"formation-option" + optionIndex} data-title={option.note}>
                                                    <Grid container alignContent={'center'} alignItems={'center'}>
                                                        {option.ref && (
                                                            <IconButton color="default" onMouseEnter={() => updateInfos(option)} sx={{ fontSize: '4px' }} >
                                                                <NoMaxWidthTooltip title={formattedInfos} >
                                                                    <InfoIcon sx={{ fontSize: '12pt' }}></InfoIcon>
                                                                </NoMaxWidthTooltip>
                                                            </IconButton>
                                                        )}
                                                        <Grid item xs={10}>
                                                            <Tooltip title={option.note}>
                                                                <Typography variant='body1'
                                                                    style={{
                                                                        backgroundColor: option.principale === true ? '#e5eef5' : 'white',
                                                                    }}
                                                                >{option.name} : {option.value} pts </Typography>
                                                            </Tooltip>
                                                        </Grid>
                                                        {option.max && (
                                                            <Grid item xs={1}>
                                                                <FormControl >
                                                                    <Select variant="standard" size='1' value={0}
                                                                        onChange={(e) => handleOptionSelect(option, selectedCarteIndex, parseInt(e.target.value))}
                                                                    >
                                                                        {option.min !== 0 && <option value={0}>0</option>}
                                                                        {[...Array(option.max - option.min + 1)].map((_, i) => (
                                                                            <MenuItem key={"formation-option-item" + i + option.min} value={i + option.min}>
                                                                                {i + option.min}
                                                                            </MenuItem>
                                                                        ))}
                                                                    </Select>
                                                                </FormControl>
                                                            </Grid>
                                                        )}
                                                        {!option.max && (
                                                            <Grid item xs={1} md={1} display="flex" justifyContent="flex-end">
                                                                <IconButton onClick={() => handleOptionSelect(option, selectedCarteIndex, 1)}>
                                                                    <AddIcon></AddIcon>
                                                                </IconButton>
                                                            </Grid>
                                                        )}
                                                    </Grid>
                                                </ListItem>
                                                <Divider variant='fullWidth' />
                                            </List>
                                        ))}
                                </Stack>
                            </CardContent>
                        </Card>
                    </Box>
                    <>
                            <SpeedDial
                            onClick={toggleDrawer(true)}
                            ariaLabel="Liste"
                            sx={{ position: 'fixed', bottom: 16, right: 16 }}
                            icon={<FormatListBulletedIcon />}
                        ></SpeedDial>
                        </>
                </Stack>

            )}
            <SwipeableDrawer
                anchor="bottom"
                open={drawerOpen}
                onClose={toggleDrawer(false)}
                onOpen={toggleDrawer(true)}
            >
                <Box sx={{ width: 350, padding: 2 }} d>
                    <Grid container justifyContent={'left'} spacing={2}>
                        <Grid item xs={3} md={3}>
                            <Typography variant="h4">Liste</Typography>
                        </Grid>
                        <Grid item xs={7} md={7}>
                            <Tooltip title="Ouvrir tous">
                                <IconButton>
                                    <OpenInFullIcon onClick={() => handleToggleAllAccordions(true)}></OpenInFullIcon>
                                </IconButton>
                            </Tooltip>
                            <Tooltip title="Fermer tous">
                                <IconButton>
                                    <CloseFullscreenIcon onClick={() => handleToggleAllAccordions(false)}></CloseFullscreenIcon>
                                </IconButton>
                            </Tooltip>
                        </Grid>
                        <Grid item xs={2} md={2}>
                            {cartes.length > 0 && (
                                <Box display="flex" justifyContent="flex-end">
                                    <Tooltip title="Effacer la liste">
                                        <IconButton className="remove-all-button" onClick={handleClickOpen} sx={{ displayPrint: 'none' }}>
                                            <CleaningServicesIcon />
                                        </IconButton>
                                        <Dialog open={open} onClose={handleClose}>
                                            <DialogTitle id="alert-dialog-title">
                                                {"Effacer la liste"}
                                            </DialogTitle>
                                            <DialogContent>
                                                <DialogContentText id="alert-dialog-description">
                                                    Etes vous sur de vouloir effacer la liste que vous avez construite ?
                                                </DialogContentText>
                                            </DialogContent>
                                            <DialogActions>
                                                <Button onClick={handleClose}>Refuser</Button>
                                                <Button onClick={handleRemoveAllCartes} autoFocus>Accepter</Button>
                                            </DialogActions>
                                        </Dialog>
                                    </Tooltip>
                                </Box>
                            )}
                        </Grid>
                        {alertRare && (
                            <Alert severity="warning" variant="outlined">
                                Attention, la valeur des formations rares ({totalRareValue}) dépasse les limites du codex
                            </Alert>
                        )}
                    </Grid>

                    <Stack spacing={1}>
                        {cartes.map((carte, carteIndex) => (
                            <Accordion disableGutters
                                onChange={() => handleToggleAccordion(carteIndex)}
                                expanded={accordionStates[carteIndex]}
                                defaultExpanded="true"
                                onClick={() => {
                                    handleCarteSelect(carteIndex);
                                    handleShowFormationOptions(carte.optionbase);
                                }}
                                sx={{ margin: "2px", border: selectedCarteIndex === carteIndex ? '2px solid #007bff' : '0px #ccc' }}
                                key={carteIndex}
                                className={`card ${selectedCarteIndex === carteIndex ? 'selected' : ''}`}
                            >
                                <AccordionSummary expandIcon={<ExpandMoreIcon />} onChange={() => handleToggleAccordion(carteIndex)}>
                                    <Grid container direction="row" justifyContent="center" alignItems="center">
                                        <Grid item xs={2} md={2} sx={{ display: 'flex', justifyContent: 'flex-start' }}>
                                            <Tooltip title="Supprimer la formation">
                                                <IconButton size="small" onClick={() => handleRemoveCarte(carteIndex)}>
                                                    <CloseRoundedIcon />
                                                </IconButton>
                                            </Tooltip>
                                        </Grid>
                                        <Grid item xs={10} md={10} display="flex" alignItems="center" alignContent="flex-start">
                                            <Typography variant="h6">
                                                {carte.formation}
                                                &nbsp;
                                                {carte.GrpPrincipal && (
                                                    <Tooltip title="Formation principale">
                                                        <StarsIcon sx={{ fontSize: 15 }} />
                                                    </Tooltip>
                                                )}
                                                {accordionStates[carteIndex] === false && (
                                                    <span sx={{ fontSize: '10pt' }}>
                                                        {' '}
                                                        ({calculateCarteValue(carte)} pts)
                                                    </span>
                                                )}
                                            </Typography>
                                        </Grid>
                                    </Grid>
                                </AccordionSummary>
                                <AccordionDetails sx={{ paddingTop: "1px" }}>
                                    <List>
                                        {carte.options.map((option, optionIndex) => (
                                            <ListItem key={optionIndex} sx={{ margin: 0, padding: 0 }}>
                                                <Typography variant="body2">
                                                    {option.selectedValue > 1 && `${option.selectedValue} `}
                                                    {option.name} - {option.value * option.selectedValue}
                                                </Typography>
                                                <Tooltip title="Supprimer l'option">
                                                    <IconButton onClick={() => handleOptionRemove(option, carteIndex)} sx={{ displayPrint: 'none' }}>
                                                        <HighlightOffIcon color="warning" sx={{ fontSize: 15 }} />
                                                    </IconButton>
                                                </Tooltip>
                                            </ListItem>
                                        ))}
                                    </List>
                                    <Typography variant="body1">Total : {calculateCarteValue(carte)}</Typography>
                                </AccordionDetails>
                            </Accordion>
                        ))}
                    </Stack>
                </Box>
            </SwipeableDrawer>

        </Container >

    );

}