import {
    AppBar, Box, Button, Dialog, DialogActions, DialogContent, DialogContentText,
    DialogTitle, Drawer, FormControlLabel, FormGroup, Grid, IconButton,
    List, ListItem, ListItemButton, ListItemIcon, ListItemText, SpeedDial,
    Switch, Toolbar, Typography, Divider
} from "@mui/material";
import PrintIcon from '@mui/icons-material/Print';
import { useContext, useRef } from "react";
import { MainContext } from "../Contexts/MainContext";
import SaveIcon from '@mui/icons-material/Save';
import FileUploadIcon from '@mui/icons-material/FileUpload';
import MenuIcon from '@mui/icons-material/Menu';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';

export default function Header() {
    const inputRef = useRef();

    const {
        cartesSansNbacti,
        AlerteAutoClose,
        handlePrint,
        selectedCodexColor,
        selectedCodex,
        calculateTotalValue,
        maxCarte,
        calculateCarteValue,
        handleOpenCodexSelection,
        handleCloseChange,
        handleClickOpenChange,
        openChange,
        exportAllCartes,
        handlePrintCaracChange,
        handlePrintListChange,
        printCarac,
        printList,
        handleFileChange,
        CopyText,handleDrawerOpen, openDrawer, handleDrawerClose
    } = useContext(MainContext);


    const handleFileUploadClick = () => {
        inputRef.current.click();
    };

    return (
        <>
            <AppBar
                position='fixed'
                sx={{
                    background: `linear-gradient(to right, #ffffff, ${selectedCodexColor} 25%)`,
                    transition: 'background-color 1s ease',
                    displayPrint: 'none' 
                }}
                color='info'
            >
                <SpeedDial
                    onClick={handleDrawerOpen}
                    ariaLabel="Menu"
                    sx={{ position: 'fixed', top: 5, right: 10 }}
                    icon={<MenuIcon />}
                />
                <Toolbar>
                    <Grid container direction="row" alignItems="center" spacing={1}>
                        {!selectedCodex && (
                            <>
                                <Grid item xs={4} md={2}>
                                    <img src={process.env.PUBLIC_URL + '/logo10_weathering.png'} alt="Logo" style={{ displayPrint: 'none', height: '50px' }} />
                                </Grid>
                                <Grid item xs={8} md={8}>
                                    <Typography variant='h4' align="center">Epic Armageddon list builder</Typography>
                                </Grid>
                            </>
                        )}
                        {selectedCodex && (
                            <>
                                <Grid item xs={6} md={2}>
                                    <Button variant="outlined" color="inherit" onClick={handleClickOpenChange}>Changer de codex</Button>
                                    <Dialog open={openChange} onClose={handleCloseChange}>
                                        <DialogTitle>{"Changer de Codex"}</DialogTitle>
                                        <DialogContent>
                                            <DialogContentText>
                                                Attention, changer de codex va effacer votre liste actuelle
                                            </DialogContentText>
                                        </DialogContent>
                                        <DialogActions>
                                            <Button onClick={handleCloseChange}>Refuser</Button>
                                            <Button onClick={handleOpenCodexSelection} autoFocus>Accepter</Button>
                                        </DialogActions>
                                    </Dialog>
                                </Grid>
                                <Grid item xs={6} md={3}>
                                    <Typography variant='subtitle1'>
                                        BLM : {maxCarte.formation} <b>{calculateCarteValue(maxCarte)}</b> pts
                                    </Typography>
                                </Grid>
                                <Grid item xs={6} md={2}>
                                    <Typography variant='subtitle1'>
                                        Formations : {cartesSansNbacti.length}
                                    </Typography>
                                </Grid>
                                <Grid item xs={6} md={2}>
                                    <Typography variant='subtitle1'>
                                        Points total : <b>{calculateTotalValue()}</b>
                                    </Typography>
                                </Grid>
                            </>
                        )}
                    </Grid>
                </Toolbar>
                <AlerteAutoClose />
            </AppBar>
            <Drawer variant="persistent" anchor="right" open={openDrawer}>
                <Box sx={{bgcolor: 'background.paper', displayPrint: 'none' }} alignContent={"center"} alignItems={"center"}>
                    <List>
                        <ListItem disablePadding>
                            <IconButton onClick={handleDrawerClose}>
                                <ChevronLeftIcon fontSize="large" />
                            </IconButton>
                        </ListItem>
                    </List>
                    <List>
                        <ListItem disablePadding>
                            <ListItemButton onClick={handleFileUploadClick}>
                                <ListItemIcon>
                                    <IconButton onClick={handleFileUploadClick}>
                                        <FileUploadIcon />
                                    </IconButton>
                                </ListItemIcon>
                                <ListItemText primary="Importer" />
                                <input
                                    type="file"
                                    ref={inputRef}
                                    style={{ display: 'none' }}
                                    onChange={handleFileChange}
                                />
                            </ListItemButton>
                        </ListItem>
                        {selectedCodex && (
                            <>
                                <ListItem disablePadding>
                                    <ListItemButton onClick={exportAllCartes}>
                                        <ListItemIcon>
                                            <IconButton onClick={exportAllCartes}>
                                                <SaveIcon />
                                            </IconButton>
                                        </ListItemIcon>
                                        <ListItemText primary="Sauvegarder" />
                                    </ListItemButton>
                                </ListItem>
                                <ListItem disablePadding>
                                    <CopyText />
                                </ListItem>
                            </>
                        )}
                    </List>
                    {selectedCodex && (
                        <>
                            <Divider />
                            <List>
                                <ListItem disablePadding>
                                    <ListItemButton onClick={handlePrint}>
                                        <ListItemIcon>
                                            <IconButton onClick={handlePrint}>
                                                <PrintIcon />
                                            </IconButton>
                                        </ListItemIcon>
                                        <ListItemText primary="Imprimer" />
                                    </ListItemButton>
                                </ListItem>
                                <ListItem disablePadding>
                                    <FormGroup>
                                        <ListItemButton>
                                            <FormControlLabel
                                                control={<Switch defaultChecked={printList} onChange={handlePrintListChange} />}
                                                label="Imprimer la liste"
                                            />
                                        </ListItemButton>
                                        <ListItemButton>
                                            <FormControlLabel
                                                control={<Switch checked={printCarac} onChange={handlePrintCaracChange} />}
                                                label="Imprimer les caractéristiques"
                                            />
                                        </ListItemButton>
                                    </FormGroup>
                                </ListItem>
                            </List>
                        </>
                    )}
                </Box>
            </Drawer>
        </>
    );
}
