import { Alert, Box, Card, CardContent, Container, Grid, List, ListItem, Typography } from '@mui/material';
import { useContext } from 'react';
import { MainContext } from '../Contexts/MainContext';

export default function PrintPage() {

    const {
        handleShowFormationOptions,
        handleCarteSelect,
        selectedCarteIndex,
        cartes,
        calculateCarteValue,
        selectedCodex,
        calculateTotalValue,
        maxCarte,
        displayPrintValue,
        alertRare,
        totalRareValue
    } = useContext(MainContext)

    return (
        <Container sx={{ displayPrint: displayPrintValue, display: 'none', height: '32cm', backgroundColor: 'white' }} maxWidth="100vw" >
            <Grid container marginTop={2} direction="row">
                {/* Colonne carte */}
                <Grid item xs={12}>
                    <Box >
                        <Card display="flex" justifyContent="center" alignContent={'center'} alignItems={'center'}>
                            <CardContent>
                                <Typography variant="h4">Liste {selectedCodex} {calculateTotalValue()} pts</Typography>
                                <Typography variant='body2'>
                                    Nombre de formations {cartes.length}
                                </Typography>
                                <Typography variant='body2'>
                                    <b>BLM : {maxCarte.formation} {calculateCarteValue(maxCarte)} pts</b>
                                </Typography>
                            </CardContent>
                        </Card>
                    </Box>
                    {alertRare && (
                        <Alert severity='warning' variant="outlined">Attention, la valeur des formations rares ({totalRareValue}) dépasse les limites du codex</Alert>
                    )}
                </Grid>
            </Grid>
            <Grid container spacing={1}>
                {cartes.map((carte, carteIndex) => (
                    <Grid item xs={6} >
                        <Box >
                            <CardContent
                                sx={{ marginTop: 0, paddingTop: 0 }}
                                key={carteIndex}
                                className={`card ${selectedCarteIndex === carteIndex ? 'selected' : ''}`}
                                onClick={() => {
                                    handleCarteSelect(carteIndex);
                                    handleShowFormationOptions(carte.optionbase);
                                    console.log("OptionBase :", carte.optionbase);
                                }}// Sélectionner une carte en cliquant dessus
                            >
                                <Grid container >
                                    <Grid item xs={10} md={10} >
                                        <Typography variant='h6'>{carte.formation}</Typography></Grid>
                                    <Grid item xs={2} md={2} >
                                    </Grid>
                                </Grid>
                                <List>
                                    {carte.options.map((option, optionIndex) => (
                                        <ListItem key={optionIndex} sx={{ margin: 0, padding: 0 }}>
                                            <Typography variant='body2'>{option.selectedValue > 1 && `${option.selectedValue} `}{option.name} - : {option.value * option.selectedValue}</Typography>
                                        </ListItem>
                                    ))}
                                </List>
                                <Typography variant='body1'>Total : {calculateCarteValue(carte)}</Typography>
                            </CardContent>
                        </Box>
                    </Grid>
                ))}
            </Grid>
        </Container >

    )
}
